code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*@import url('https://fonts.googleapis.com/css?family=Nunito');*/

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body {
  font-family: 'Maven Pro';
}
.CandleStickTemplate {
    font-family: sans-serif;
    text-align: center;
}

.sidebar {
    background-color: #171742;
}

.content {
    background: rgb(26, 30, 80);
    background: linear-gradient(
            45deg,
            rgba(26, 30, 80, 1) 0%,
            rgba(46, 87, 126, 1) 50%,
            rgba(57, 108, 144, 1) 100%
    );
    color: white;
}

.chart {
    background-color: #1f234b;
    color: white;
}

.candle {
    stroke-width: 1;
}

.candle.up {
    fill: #81dfc4;
    stroke: #81dfc4;
}

.candle.down {
    fill: #d75b6d;
    stroke: #d75b6d;
}

.wick {
    stroke-width: 1.5;
}

.wick.up {
    stroke: #81dfc4;
}

.wick.down {
    stroke: #d75b6d;
}

.cross_hair {
    stroke: white;
    stroke-opacity: 70%;
    stroke-dasharray: 3;
}

#chart{
    text-align: center;
    padding-top: 50px;
}
.arc text {
    font: 10px sans-serif;
    text-anchor: middle;
}

.pieBox{
    display: inline-block;
    height: auto;
}

#mainTooltip {
    position: absolute;
    min-width :90px;
    height: 40px;
    padding: 0 0 25px 0;
    background-color: rgba(0,0,0,0.6);
    border-radius: 50px;
    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.4);
    pointer-events: none;
    fill: #fff;
    color: #fff;
    text-anchor: middle;
    text-align: center;
    font-weight: bold;
    z-index: 9999;
}

#mainTooltip:before {
    border: solid;
    border-color: rgba(0,0,0,0.6) transparent;
    border-width: 10px 10px 0 10px;
    top: 100%;
    content: "";
    left: 43%;
    position: absolute;
    z-index: 99;
}

#mainTooltip.hidden {
    display: none;
}
